@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  text-align: center;
}

.app-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  padding: 8px;

  background-color: #ffdb91;

  font-family: 'Balsamiq Sans', sans-serif;
  font-size: calc(18px + 3vmin);
  color: #222;
}

.header-text-mini {
  margin-top: 10px;

  font-size: calc(18px + 1vmin);
  color: #333;
}

.lang-button {
  opacity: 0.7;
  transition: 200ms all ease-in;

  border: none;
  background: transparent;

  font-size: calc(14px + 1vmin);
}

.lang-button:hover {
  opacity: 1;
}

.main-content {
  width: 90%;
  max-width: 1000px;

  margin: 0 auto 10px auto;
  padding: 10px;

  line-height: 1.5;

  text-align: justify;
}

.form-label {
  display: flex;
  justify-content: center;
  flex-basis: 50%;

  margin-left: -10px;
}

.form-text-area {
  width: 90%;
  resize: none;

  margin: 10px;
}

.form-input {
  max-width: 80%;

  margin: 10px;
}

.gradient-border {
  margin-bottom: 4px;
  border: 0;
  height: 4px;

  background-image: linear-gradient(to right, rgba(238, 188, 90, 0.95) 25px, transparent 85%);
}

.footer-block {
  margin-top: auto;
  border-top: 2px solid #f5e5bc;
  padding-bottom: 12px;
  padding-top: 12px;

  color: #504a4a;
  font-family: 'Balsamiq Sans', sans-serif;
  font-size: 14px;
}

.gh-logo {
  fill: #6d6c6c;
  transition: 0.2s;
}

.gh-logo:hover {
  fill: #322f2f;
  transition: 0.2s;
}
